import { VueConstructor } from "vue/types/umd";

export class InterestItemInfo {
  public title: string;
  public text: string;
  public image: VueConstructor | string | null;

  public constructor(
    title: string,
    text: string,
    image: VueConstructor | string | null
  ) {
    this.title = title;
    this.text = text;
    this.image = image;
  }
}
